.gallary {
  padding-top: 30px;
  background-image: linear-gradient(to bottom, $mainBgColor 60px, $thirdTexColor 60px, $thirdTexColor 100%);
}


.gallery img {
  height: 100%;
}

.gallery {
  // background: #fff;
  max-width: 1200px;
}

/* clearfix */
.gallery:after {
  content: '';
  display: block;
  clear: both;
}

/* ---- gallery-item ---- */

.gallery-sizer,
.gallery-item {
  //   width: 45px;
  width: 15%;
}

.gallery-item {
  //   height: 120px;
  margin-bottom: 2.5px;
  float: left;

  transition-duration: 250ms;
  transition-property: transform;

  &:hover {
    transform: scale(1.1);
    z-index: 10;
  }
}

