.map__bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    opacity: 1;
    visibility: visible;
    background-color: rgba(0, 0, 0, 0.2);
    @include transition (opacity);
    overflow-y: auto;
    transition: all 0.8s ease 0s;
}

.map__bg.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
}

.map__bg .map {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
}

.map__bg.is-hidden .map {
    transform: translate(-50%, -50%) scale(0.1) rotate(180deg);
}

.map {
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 40px;
    // transform: translate(-50%, -50%) scale(0);
    border-radius: 22px;
    background: $secondBgColor;
    width: calc(100vw - 30px);
    height: calc(100vh - 30px);
    opacity: 0;
    transition: all 0.8s ease 0s;

    @include tablet {
        width: calc(100vw - 30px);
        height: calc(100vh - 50px);
    }

    @include desktop {
        width: 1000px;
        height: calc(100vh - 100px);
    }


    // transition: 250ms all;
}

// .map.active {
//     transform: translate(-50%, -50%) scale(1);
//     transition: 250ms all;
// }

.close-map {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: $currentBgGolor;
    transition: background-color linear 250ms;

    &:hover,
    &:focus {
        background-color: $mainBgColor;
    }
}

.g-map {
    width: 100%;
    height: 100%;
    border-radius: 7px;
}

