.popup__bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    opacity: 1;
    visibility: visible;
    background-color: rgba(0, 0, 0, 0.2);
    @include transition (opacity);
    overflow-y: auto;
    transition: all 0.8s ease 0s;
}

.popup__bg .popap {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
}

.popup__bg.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
}

.popup__bg.is-hidden .popup {
    transform: translate(-80%, -100%) scale(0.1) rotate(180deg);
}

.popup__bg .popup {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
}


.popup {
    position: absolute;
    top: 50%;
    left: 50%;
    background: #fff;
    border-radius: 10px;
    width: calc(100vw - 30px);
    max-width: 450px;
    opacity: 0;

    @include tablet {
        width: 450px;
    }

    padding: 25px;
    transition: 0.5s all;
}

