.popup label {
    width: 100%;
    margin-bottom: 25px;
    display: flex;
    flex-direction: column-reverse;
}

.popup .label__text {
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 500;
    color: $secondBgColor;
    margin-bottom: 5px;
}

.popup input {
    height: 45px;
    font-size: 18px;
    border: none;
    outline: none;
    border-bottom: 1px solid $secondBgColor;
}

.popup__text {
    color: $mainTextColor;
}

.popup input:focus {
    border-bottom: 1px solid $currentBgGolor;
}

.popup input:focus+.label__text {
    color: $currentBgGolor;
}

.popup textarea {
    resize: none;
    width: 100%;
    height: 150px;
    border: none;
    outline: none;
    border-bottom: 1px solid $secondBgColor;
    font-size: 18px;
    padding-top: 5px;
}

.popup textarea:focus {
    border-bottom: 1px solid $currentBgGolor;
}

.popup textarea:focus+.label__text {
    color: $currentBgGolor;
}

.button-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: 32px;

    @media screen and (min-width: 768px) {
        flex-direction: row;
        margin-top: 60px;
    }

}

