.products {
    background-color: $thirdTexColor;
   
}

.products__lable-text {
    font-family: Titan One;
   font-weight: 400;
   font-size: 18px;
   line-height: calc(21/18);
   color:$secondTextColor ;
   text-transform: uppercase;
   text-align: center;
   margin-bottom: 15px;
   @include tablet {
    font-size: 26px;
   line-height: calc(30/26);
   }
   @include desktop {
    font-size: 30px;
    line-height: calc(34/30);
   }
}

.products__title--main {
    font-family: Titan One;
    font-weight: 400;
    font-size: 34px;
    line-height: calc(39/34);
    letter-spacing: 0.04em;
    color:$mainTextColor;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 8px;
    @include tablet {
    font-size: 48px;
    line-height: calc(55/48);
    margin-bottom: 20px;
}
    @include desktop {
    font-size: 58px;
    line-height: calc(66/58);
    
    }

}

.products__title--second {
    font-family: Titan One;
    font-weight: 400;
    font-size: 20px;
    line-height: calc(23/20);
    color: $thirdTexColor ;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 32px;
    @include desktop {
    font-size: 30px;
    line-height: calc(34/30);
        }
    
    
}

.products__title--second::after {
    content:"";
    background-image: url(../images/products/eclips.png);
    width: 26px;
    height: 6px;
    display: block;
    margin: 0 auto;
    margin-top: 32px;}

  

.products__text {
    font-family: DM Sans;
    font-weight: 700;
    font-size: 14px;
    line-height: calc(21/14);
    letter-spacing: 0.04em;
    color: $thirdTexColor ;
    text-align: center;
    margin-bottom: 32px;
    @include tablet {
    line-height: calc(25/14);    
    }
    @include desktop {
    font-size: 16px;
    line-height: calc(28/16);
    
            }

    
}

.products__list {
    display: flex;  
    flex-direction: column;
    justify-content: center;
    align-items: center;
  
    
    
    @include tablet {
    flex-direction: row;
    justify-content: center;
     
    gap: 16px;   
    }
    @include desktop {
    gap: 32px;
    }
}
.products__item {
    @include mobile {
    width: 280px;
    height: 489px;}

    @include tablet {
    width: calc((100% - 32px) / 3);
        
    height: 473px;
                 }
    @include desktop {
        width: calc((100% - 64px) / 3);
        
        height: 535px;
                 }

}

.products__item {
 
 border-radius: 24px;
 padding: 160px 39px 40px 41px;
 
 position: relative;
 margin-top: 140px;
 @include tablet {
    margin-top: 160px;
    padding: 110px 32px 40px 32px;
 }
 @include desktop {
    margin-top: 280px;
    padding: 165px 62px 60px 61px;
 }
 
}

