.products__item::before {
  content: "";  
  width: 280px;
  height: 280px;

  position: absolute;
  top:-140px;
  left:5px;
}

  .products__item::before {
    @include tablet {
    content: "";
    position: absolute;
    width: 224px;
    height: 224px;
    top:-140px;
    left:5px;
}
  }

  .products__item::before {
    @include desktop {
    content: "";
    position: absolute;
    width: 368px;
    height: 394px;
    top:-270px;
    left:5px;
}
  }

.products__button {
    width: 40px;
    height: 40px;
    background-color: $thirdTexColor;
    border-radius: 50%;
    
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    transition-property: background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-delay: 0ms;
   }

.products__button:hover,
.products__button:focus {
    background-color: $mainBgColor;
}

.button__icon {
    fill: $mainTextColor;
    
}

.products__backdrop {
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);
    transition: opacity 300ms, visibility 300ms;
  }
  
  .modal--first {
    background-color: $secondTextColor;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scaleY(1) rotate(0);
    transition: transform 300ms;
    z-index: 20;
  
    @include mobile {
      width: 100vw;
      height: 20vh;
    }
  
    @media screen and (min-width: 480px) {
      width: 450px;
      height: auto;
    }
  }
  .modal--second {
    background-color: $thirdBgColor;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scaleY(1) rotate(0);
    transition: transform 300ms;
    z-index: 20;
  
    @include mobile {
      width: 100vw;
      height: 20vh;
    }
  
    @media screen and (min-width: 480px) {
      width: 450px;
      height: auto;
    }
  }

  .modal--thirth {
    background-color: $fourthBgColor;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scaleY(1) rotate(0);
    transition: transform 300ms;
    z-index: 20;
  
    @include mobile {
      width: 100vw;
      height: 20vh;
    }
  
    @media screen and (min-width: 480px) {
      width: 450px;
      height: auto;
    }
  }

  .products__backdrop.is-hidden .modal--first {
  
    transform: translate(-50%, -50%) scaleY(0) rotate(180deg);
  }
  .products__backdrop.is-hidden .modal--second {
  
    transform: translate(-50%, -50%) scaleY(0) rotate(180deg);
  }
  .products__backdrop.is-hidden .modal--thirth {
  
    transform: translate(-50%, -50%) scaleY(0) rotate(180deg);
  }
  
  .modal-close-button {
    border: 1px solid transparent;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #ffb8ca;
    align-items: center;
    justify-content: center;
    padding: 0px;
    position: absolute;
    top: 8px;
    right: 8px;
    cursor: pointer;
  }

  
  
  .is-hidden {
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
  }
  
  .products__modal-form {
   padding: 40px 40px 40px 40px;
  }
  
  .products__modal-title {
    font-family: Titan One;
    font-weight: 400;
    font-size: 20px;
    line-height: calc(23/20);
    color: $mainBgColor;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    font-weight: 700;
    padding-bottom: 12px;
  }
  
  .products__modal-field {
    display: block;
    margin: 0;
  }
  
  .products__modal-label {
    display: block;
    font-size: 16px;
    font-weight: 400px;
    line-height: 1.5;
    letter-spacing: 0.01em;
    color: $fifthTextColor;
    padding-bottom: 4px;
    padding-top: 10px;
  }
  
  .icon-close-button {
    fill: $mainTextColor;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  }


